<template>
  <div>
    <template v-if="type===1">
      <Header title="实名认证" />
    </template>
    <template v-if="type===2">
      <Header title="充值" />
    </template>
    <template v-if="type===3">
      <Header title="加入会员" />
    </template>
    
    <div class="toast">
      <template v-if="showing">
        <div class="toastIcon flex_col">
          <template v-if="showing">
            <img src="~@/assets/dygvd.png" alt="">
          </template>
          <p v-if="type===1">认证成功</p>
          <p v-if="type===2">充值成功</p>
          <p v-if="type===3">加入成功</p>
        </div>
      </template>
      <template v-if="!showing">
        <div class="toastIcon flex_col">
          <img src="~@/assets/xbsgw.png" alt="">
          <p v-if="type===1">认证失败</p>
          <p v-if="type===2">充值失败</p>
          <p v-if="type===3">加入失败</p>
        </div>
      </template>

      <template v-if="showing">
        <div class="btn flex_row">
          <van-button plain type="danger">
            <span>完成</span>
          </van-button>
        </div>
      </template>
      <template>
        <div class="btn flex_space">
          <van-button  type="danger" >
            <span v-if="" class="continue">{{type===1?'继续认证':'继续'}}</span>
          </van-button>
          <van-button plain type="danger" @click="toBack">
            <span>返回</span>
          </van-button>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
export default {
  name: 'Task', 
  components: {
    Header
  },
  data () {
    return { 
      showing: false,
      type: 0    
    }
  },
  mounted () {
    this.type = this.$route.params.type
    console.log(this.type)
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .toast{
    width: 100%;
    min-height: 93vh;
    background: #fff;
  }
  .toastIcon{
    width: 100%;
    height: 4.46rem;
    img{
      width: 1.35rem;
      height: 1.35rem;
    }
    p{
      font-size: 0.35rem;
      color: #222;
      padding-top: 0.3rem;
    }
  }
  .btn{
    width: 100%;
    height: 0.9rem;
    padding: 0 1.14rem;
    button{
      width: 1.92rem;
      border-radius: 0.9rem;      
      span{
        color: #e64535;
      }
      .continue{
        color: #fff;
      }
    }
    .van-button{
      height: 0.8rem;
      line-height: 0.8rem;
    }
  }
</style>



