import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/page/Home'
import Task from '@/page/Task'
import Share from '@/page/Share'
import Notice from '@/page/Notice'
import NoticeDet from '@/page/NoticeDet'
import TaskList from '@/page/TaskList'
import TaskDetail from '@/page/TaskDetail'
import ShopDetail from '@/page/ShopDetail'
import PastShop from '@/page/PastShop'
import LookShare from '@/page/LookShare'
import PayMents from '@/page/PayMents'
import AddressList from '@/page/AddressList'
import NewAddress from  '@/page/NewAddress'
import Mine from  '@/page/mine'
import Attestation from  '@/page/Attestation'
import Customer from  '@/page/Customer'
import Toast from '@/page/Toast'
import TeamList from '@/page/TeamList'
import MyJoin from '@/page/MyJoin'
import Record from '@/page/Record'
import About from '@/page/About' 
import Infomation from '@/page/Infomation'
import InfoDetail from '@/page/InfoDetail'
import GeneCode from '@/page/GeneCode'
import Recharge from '@/page/Recharge'
import Withdraw from '@/page/Withdraw'
import BankWithdraw from '@/page/BankWithdraw'
import JoinMember from '@/page/JoinMember'
import Balance from '@/page/Balance'

import DreamTaskList from '@/page/DreamTaskList'
import DreamShopDetail from '@/page/DreamShopDetail'
import DreamPastShop from '@/page/DreamPastShop'
import DreamLookShare from '@/page/DreamLookShare'
import DreamPayMents from '@/page/DreamPayMents'

import RegisterNext from '@/components/RegisterNext'//用户完善信息

Vue.use(Router)
const router=new Router({
  routes: [
    {
      path: '/',
      name: 'Home',     
      component: Home      
    },
    {
    	path:'/joinmember/:id',
    	name:'JoinMember',
    	component:JoinMember
    },
    {
      path: '/task',
      name: 'Task',      
      component: Task
    },
    {
      path: '/share',
      name: 'Share',      
      component: Share
    },
    {
      path: '/notice',
      name: 'Notice',
      component: Notice
    },
    {
      path: '/noticeDet',
      name: 'NoticeDet',
      component: NoticeDet
    },
    {
      path: '/taskList',
      name: 'TaskList',      
      component: TaskList
    },
    {
      path: '/dreamtaskList',/*晒单分享 */
      name: 'DreamTaskList',      
      component: DreamTaskList
    },
    {
      path: '/taskDetail',
      name: 'TaskDetail',      
      component: TaskDetail
    },
    {
      path: '/shopDetail/:id',
      name: 'ShopDetail',      
      component:ShopDetail
    },
    {
      path: '/dreamshopDetail/:id',/*晒单分享 */
      name: 'DreamShopDetail',      
      component:DreamShopDetail
    },
    {
      path: '/pastShop',
      name: 'PastShop',      
      component: PastShop
    },
    {
      path: '/dreampastShop',/*晒单分享 */
      name: 'DreamPastShop',      
      component: DreamPastShop
    },
    {
      path: '/dreamlookShare',/*晒单分享 */
      name: 'DreamLookShare',      
      component: DreamLookShare
    },
    {
      path: '/lookShare',
      name: 'LookShare',      
      component: LookShare
    },
    {
      path: '/payMents',
      name: 'PayMents',      
      component: PayMents
    },
    {
      path: '/dreampayMents/:id',
      name: 'DreamPayMents',      
      component: DreamPayMents
    },
    {
      path: '/addressList',
      name: 'AddressList',      
      component: AddressList
    },
    {
      path: '/newAddress',
      name: 'NewAddress',      
      component: NewAddress
    },
    {
      path: '/mine',
      name: 'Mine',      
      component: Mine
    },
    {
      path: '/attestation/:id',
      name: 'Attestation',      
      component: Attestation
    },
    {
      path: '/customer',
      name: 'Customer',      
      component: Customer
    },
    {
      path: '/toast',
      name: 'Toast',      
      component: Toast
    },
    {
      path: '/teamList/:id',
      name: 'TeamList',      
      component: TeamList
    },
    {
      path: '/myJoin/:id',
      name: 'MyJoin',      
      component: MyJoin
    },
    {
      path: '/record/:id',
      name: 'Record',      
      component: Record
    },
    {
      path: '/about',
      name: 'About',      
      component: About
    },
    {
      path: '/infomation',
      name: 'Infomation',      
      component: Infomation
    },
    {
      path: '/infoDetail',
      name: 'InfoDetail',      
      component: InfoDetail
    },
    {
      path: '/geneCode/:id',
      name: 'GeneCode',      
      component: GeneCode
    },
    {
      path: '/recharge/:id',
      name: Recharge,
      component: Recharge
    },
    {
      path: '/withdraw/:id',
      name: Withdraw,
      component: Withdraw
    },
    {
      path: '/bankWithdraw',
      name: BankWithdraw,
      component: BankWithdraw
    },
    {
      path: '/balance',
      name: Balance,
      component: Balance
    },
    /*{
	    path: 'orderDetails/:id',
	    name: 'orderDetails',
	    component: orderDetails,
		}*/
  ]
})


export default router



