<template>
  <div>
    <Header title="团队列表"/>
    <van-tabs
      v-model="active" 
      swipeable 
      @change="tabHead"
    >
      <van-tab  
        v-for="(it,index) in nav"
        :key="index"
        :title="it.title"
      >
        <template>
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
            <ul class="shopBox padd" v-for="(item,index) in info" :key="index">
              <li class="shoplis flex" >
                <div class="bigImg">
                  <img :src="item.headimg" alt="">
                </div>
                <div class="rightShop flex1">
                  <div class="shopTit word">
                   {{item.nickname}}<!--xingming-->
                  </div>
                  <p class="p1">编号：<!--GFTY9867-->{{item.recommendid}}</p>
                  <p class="p2">注册时间：{{item.addtime}}<!--shijian --></p>
                </div>
              </li>
            </ul>
          </van-list>
        </template>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return { 
      active: 0,
      info:'',
      nav:[
        {title: '一级', id: 1},
        {title: '二级', id: 2}
      ],
      list: [],
      loading: false,
      finished: false,
    }
  },
  mounted () {
		//console.log("qq")
		var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myteam",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:1
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
				    	that.info=result.data.onelevel
				   // 	console.log(result)
						//	console.log(result.data.duobaos)
    		}
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    tabHead (index,title) {
      console.log(index,title)
			if(index==0){
      	//alert(title)
      	var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myteam",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:1
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
				    	that.info=result.data.onelevel
				   // 	console.log(result)
						//	console.log(result.data.duobaos)
    		}
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }else if(index==1){
      //	alert(title)
      	var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myteam",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:2
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.twolevel
					    	console.log(result)
								console.log(result.data.duobaos)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
      
      
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  
  .shoplis{
    background: #fff;
    margin-top: 0.3rem;
    padding: 0.4rem .3rem;
    box-shadow: 0 0 0.1rem #ddd;
    .bigImg{
      width: 1.26rem;
      height: 1.26rem;
      border-radius: 1.26rem;
      margin-right: 0.4rem;
    }
    .rightShop{
      height: 100%;
      .shopTit{
        width: 100%;
        font-size: 0.32rem;
        line-height: 0.5rem;
        color: #e64535;
      }
      .p1{
        font-size: 0.26rem;
        color: #333;
        font-weight: bold;
        line-height: 0.44rem;
      }
      .p2{
        font-size: 0.24rem;
        color: #666;
      }
    }
  }
</style>



