<template>
  <div>
    <Header title="提现"/>
    <div class="rechargeMain padd">
      <div class="maskText" id="maskTextId">提现金额：{{this.$route.query.money*0.2}}</div>
      <template v-if="type=='1'">
        <div class="balanceBox ">
          <div class="inputBox flex_row">
            <van-field 
              v-model="bankName"
              label="支付宝账号："
              placeholder=""
            />
          </div>
        </div> 
      </template>
      <template v-if="type=='3'">
        <div class="balanceBox ">
          <div class="inputBox flex_row">
            <van-field 
              v-model="bankName"
              label="银行名称："
              placeholder=""
            />
          </div>
          <div class="inputBox flex_row">
            <van-field 
              v-model="openBank"
              label="开户行："
              placeholder=""
            />
          </div>
          <div class="inputBox flex_row">
            <van-field 
              v-model="account"
              label="账   号："
              placeholder=""
            />
          </div>
          <div class="inputBox flex_row">
            <van-field 
              v-model="name"
              label="姓   名："
              placeholder=""
            />
          </div>
          <div class="inputBox flex_row">
            <van-field 
              v-model="phone"
              label="预留手机号："
              placeholder=""
            />
          </div>
        </div> 
      </template>    
    </div>
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row" @click="toRecharge">
        <span>确认提现</span>
      </van-button>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'BankWithdraw',
  components: {
    Header
  },
  data () {
    return {           
      bankName: null,
      openBank: null,
      account: null,
      name: null,
      phone: null,
      flag: 0,
      radio: 0,
      type: this.$route.query.type
    }
  },
  mounted () {
		console.log(this.$route.query.id)
		console.log(this.$route.query.money)
  },
  methods: {  
    toRecharge () { 
    	alert("zhifub111")
    	var t=this.type
    	axios({
      	url:"http://192.168.1.192/duobao/api.php/member/withdraw",
      	method:"post",
      	params:{
      			type:t,
						money:this.$route.query.money,
						id:this.$route.query.id,
      	}
      }).then(response => {
      	(this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 

  .rechargeMain{
    width: 100vw;
    height: 90vh;
    background: #fff;
    .maskText{
      color: #666;
      font-size: 0.28rem;
      padding: 0.3rem  0.3rem 0.1rem;
      color: #e64535;
    }
    .balanceBox{
      width: 100%;        
      .balance{
        p:nth-of-type(1){
          font-size: 0.26rem;
          color: #999;
        }
        p:nth-of-type(2){
          color: #e64535;
          font-size: 0.28rem;
          font-weight: bold;
          padding-top: 0.1rem;
        }
      }
      .inputBox{
        width: 100%;
        border-bottom: 1px solid #dedede;
      }
    }
  
  }
  .btmBtn{
    width: 100%;
    height: 1.4rem;
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0rem;
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: .1rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
  
</style>



