<template>
  <div>
    <Header title="任务列表"/>
    <van-tabs
      v-model="active" 
      swipeable 
      @change="tabHead"
    >
      <van-tab  
        v-for="(it,index) in nav"
        :key="index"
        :title="it.title"
      >
        <template>
          <ul class="shopBox padd" v-for="(item,index) in info"  :key="index">
            <li class="shoplis flex" ><!-- v-for="item in infoo"-->
              <div class="bigImg">
                <img :src="item.fund_pic" alt="">
              </div>
              <div class="rightShop flex1">
                <div class="shopTit word_wrap">
                  {{item.fund_name}}<!--fnjfghg让女方家人看过国家而根据国际恐怖国家控股今日fnjfghg让女方家人看过国家而根据国际恐怖国家控股今日-->
                </div>
                <div class="lineBox">
                  <span class="line" style="width: 60%;"></span>
                </div>
                <div class="numberShop flex">
                  <div class="numlis flex1 flex_col">
                    <b>{{item.joinnumber}}</b>
                    <span>已参与</span>
                  </div>
                  <div class="numlis flex1 flex_col">
                    <b>{{item.neednumber}}</b>
                    <span>总需人数</span>
                  </div>
                  <div class="numlis flex2 flex_col">
                    <b>{{item.surplusnumber}}</b>
                    <span>剩余</span>
                  </div>
                </div>
                <router-link class="flex" :to="'DreamShopDetail/'+ item.id">
                  <van-button :class="{'gray':active==1}"><em>参与夺宝</em></van-button>
                </router-link>
              </div>
            </li>
          </ul>
        </template>

      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return { 
      active: 0,
      info:null,
      nav:[
        {title: '正在抢购', id: 1},
        {title: '已经结束', id: 2}
      ]
    }
  },
  mounted () {
  	
  	var that=this
  	axios({
      	url:"http://192.168.1.192/duobao/api.php/fund/fund",
      	method:"post",
      	params:{
      		type:0
      	}
      })
    .then(
		   function(result){
					    //	console.log(result)
					    	that.info=result.data.funds
								console.log(result.data.funds)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    tabHead (index,title) {
      console.log(index,title)
      if(index==0){
      	//alert(title)
      	var that =this
		axios({
      	url:"http://192.168.1.192/duobao/api.php/fund/fund",
      	method:"post",
      	params:{
      		type:index
      	}
      })
    .then(
		   function(result){
					    //	console.log(result)
					    	that.info=result.data.funds
								console.log(result.data.funds)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }else if(index==1){
      //	alert(title)
      	var that=this
      	axios({
	      	url:"http://192.168.1.192/duobao/api.php/fund/fund",
	      	method:"get",
	      	params:{
	      		type:1
	      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.funds
					    	console.log(result)
								console.log(result.data.funds)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }
    },onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
      }
  }
   
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  
  .shoplis{
    background: #fff;
    margin-top: 0.3rem;
    padding: 0.4rem .3rem;
    box-shadow: 0 0 0.1rem #ddd;
    .bigImg{
      width: 2.2rem;
      height: 2.8rem;
      border: 1px solid #eee;
      border-radius: 0.1rem;
      margin-right: 0.2rem;
    }
    .rightShop{
      height: 100%;
      .shopTit{
        width: 100%;
        font-size: 0.26rem;
        line-height: 0.4rem;
        color: #000;
      }      
      .lineBox{
        width: 100%;
        margin: 0.2rem 0;
        height: 0.1rem;
        background: #ddd;
        border-radius: .1rem;
        position: relative;
        .line{
          height: .1rem;
          display: block;
          position: absolute;
          left: 0;
          top:0;
          background: #f5c243;
          border-radius: .1rem;
        }
      }
      .numberShop{
        width: 100%;
        .numlis{
          &:nth-of-type(1){
            b{
              color: #e54432;
            }
          }
          &:nth-of-type(3){
            b{
              color: #f1c53e;
            }
          }
          b{
            font-size: 0.24rem;
            color: #666;
            font-weight: bold;
            line-height: 0.4rem;
          }
          span{
            font-size: 0.24rem;
            color: #999;
          }
        }
      }
      .van-button{
        margin-top: 0.2rem;
        height: 0.6rem;
        line-height: 0.6rem;        
        border: 1px solid #e54633;
        background: #fbe1e0;
        em{
           color: #e54633;
           height: 100%;
           font-style: normal;
        }
      }
      .gray{
        border: 1px solid #ddd;
        background: #ddd;
        em{
          color: #666;
          font-style: normal;
        }
      }
    }
  }
</style>



