<template>
  <div>
    <Header title="结算支付" />
    <div class="address flex" @click="addList">
      <div class="addIcon"></div>
      <div class="addText flex1">
        <p class="p1 flex_space">
          <span>小区名称：花园新村</span>
          <span>010-84903732</span>
        </p>
        <p class="p2">地址：河南省郑州市金水区北邻路街道花园路花园
        新村22号10</p>
      </div>
      <div class="icons flex_row">
        <van-icon name="arrow" />
      </div>
    </div>
    <ul class="payUl">
      <li class="border flex_alicen">
        <!--<span v-model="num">商品数量：<i>1</i>件-->
        	<span>商品数量：<input @blur="shu" v-model="shuzhi" style="width: 40px;"/>件
        <i></i></span>
      </li>
      <li class="border flex_alicen">
        <span>应付金额：<i>10</i>夺宝币
        </span>
      </li>
      <li class="border flex_alicen">
        <span>幸运号码：<i>12 34 52 34 23 98</i></span>
      </li>
      <li class="flex_space border">
        <span>余额支付（余额：200）</span>
        <span>20夺宝币</span>
      </li>
    </ul>
    <div class="chosePay">
      <div class="payTit">选择支付方式</div>
      <van-radio-group v-model="radio">
        <van-cell-group class="flex_alicen pdLf">
          <img class="payIco" src="~@/assets/duhgs.png"/>
          <van-cell title="支付宝" clickable @click="radio = 1">
            <van-radio name="1" />
          </van-cell>          
        </van-cell-group>
        <van-cell-group class="flex_alicen pdLf">
          <img class="payIco" src="~@/assets/sxcdk.png"/>
          <van-cell title="微信" clickable @click="radio = 2">
            <van-radio name="2" />
          </van-cell>
        </van-cell-group>
      </van-radio-group>
    </div>
    <div class="btmBtn flex_row" @click="pay">
      <van-button :disabled="false" type="danger"><span>确认支付</span></van-button>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'PayMents', 
  components: {
    Header
  },
  data () {
    return { 
      radio: 0,
      shuzhi:'1'
    }
  },
  mounted () {
  	console.log(this.$route.query.userid)
		console.log(this.$route.query.id)
  },
  methods: {
    addList () {
      this.$router.push({ name: 'AddressList'})
    },
    shu:function(){
			if(!(/^[1-9]*[0-9]*$/.test(this.shuzhi)&&this.shuzhi>0)){ 
        alert("cuol")
    	}
    },
    pay(){
    //	alert(this.radio)
    //	alert(this.shuzhi)
    	var that=this
    	axios({
      	url:"http://yg.91chihu.com/api.php/fund/fundjoin",
      	method:"get",
      	params:{
      			duobaoid:this.$route.query.id,
      			userid:this.$route.params.id,
      			num:this.shuzhi,
						type:this.radio
      	}
      }).then(response => {
      	(that.info = response.data.member)
      	//console.log(response.balance)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    	
    }
  }
}
</script>
<!--<script type="text/javascript">
	methods: {
    shu(){
        if(this.count < 0){
           return "不能为负";
        }
    }
}
</script>-->

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  .pdLf{
    padding-left: 0.3rem;
  }
  .btmBtn{
    width:100%;
    height: 1.88rem;
    background: #fff;
    padding: 0 0.3rem;
    position: fixed;
    left:0;
    bottom: 0;
    button{
      width:100%;
      height: 0.88rem;
      span{
        color: #fff;
      }
    }
  }  
  .address{
    width: 100%;
    padding: 0.3rem;
    background: #fff;
    margin-bottom: 0.2rem;
    .addIcon{
      width: 0.48rem;      
      background: url('~@/assets/sxdcl.png') no-repeat center;
      background-size: 0.36rem auto;
      margin-right: 0.2rem;
    }
    .addText{
      p{
        font-size: 0.24rem;
        color: #000;
        padding-bottom: 0.06rem;
        span{
          font-size: 0.26rem;         
        }
      }
      .p2{
        line-height: 0.36rem;
      }
    }
    .icons{
      width: .3rem;
      margin-left: 0.2rem;
    }
  }
  .payUl{
    width:100%;
    background: #fff;
    li{
      width: 100%;
      height: 1rem;  
      padding: 0 0.3rem;  
      span{
        font-size: 0.3rem;
        color: #333;
        i{
          color: #e64535;
          font-size: 0.3rem;
        }
      }
    }
  }
</style>



