<template lang="html">
  <div class="main" :style="{ height: bodyHeight + 'px' }">
    <div class="logo">
      <img src="../assets/images/logo@2x.png">
    </div>
    <section>
      <div class="tzh-input name-input">
        <input type="text" class="border" placeholder="请输入您的姓名" v-model="name" @focus='closeModal'>
      </div>
      <div class="tzh-input company-input">
        <input type="text" class="border" placeholder="请输入您的公司名称" v-model="company" @focus='closeModal'>
      </div>
      <div class="tzh-input position-input">
        <input type="text" class="border" placeholder="请输入您的职位(选填)" v-model="position" @focus='closeModal'>
      </div>
      <div class="tzh-input birthday-input">
        <input type="text" class="border" placeholder="请输入您的生日(选填)" v-model="birthday" @focus='openDate' readonly>
      </div>
      <div class="tzh-input address-input">
        <input type="text" class="border" placeholder="请选择所在地" v-model="address" @focus='onFocus'  readonly>
      </div>
    </section>
    <mt-button type="primary" size="large" @click.native="register">提交</mt-button>
    <vue-pickers :show="show"
    :columns="columns"
    :selectData="slots"
    @cancel="close"
    @confirm="confirmFn"></vue-pickers>
    <mt-popup v-show='showDate' position="bottom">
      <van-datetime-picker
        v-model="time"
        type="date"
        :min-date="startDate"
        :max-date="endDate"
        @confirm='dateConfirm'
        @cancel='dateCancel'
      />
    </mt-popup>
  </div>
</template>

<script>
import utils from './common/format.js'
import validator from './mixins/formValidator.js'; // 引入公共表单校验规则
import VuePickers from 'vue-pickers'
import { DatetimePicker } from 'vant';
import 'vant/lib/vant-css/index.css';

export default {
  data(){
    return {
      time:new Date('1980/01/01'),
      showDate:false,
      show:false,
      name:'',
      company:'',
      position:'',
      birthday:'',
      address:'',
      bodyHeight:'',
      ids:[''],//办事处id
      officeId:'',//选中的办事处id
      startDate:new Date('1920/01/01'),
      endDate:new Date(),
      columns: 1, // picker的列数
      slots:{
          data1: []
      },
      auditStatus:''
    }
  },
  mixins:[validator],
  components:{
    VuePickers,
    'van-datetime-picker':DatetimePicker},
  mounted(){
    this.bodyHeight=document.documentElement.clientHeight
    this.getData('/web/config/office','get').then(data=>{
      if(data.data.returnCode==200){
        for(var index in data.data.data){
          this.$set(this.slots.data1,index,{text:data.data.data[index].officeName,value:data.data.data[index].id})
          //this.slots.data1.push({text:data.data.data[index].officeName,value:data.data.data[index].id})
          this.ids.push(data.data.data[index].id)
        }
      }else{
        this.$toast(data.data.msg)
      }
      // this.slots[0].values=[data.data.data.officeName
    })
    this.getData('/web/member/info','get').then(data=>{
      if(data.data.returnCode==200){
        this.auditStatus=data.data.data.auditStatus
        this.name=data.data.data.nickName?data.data.data.nickName:''
        this.company=data.data.data.companyName?data.data.data.companyName:''
        this.position=data.data.data.position?data.data.data.position:''
        this.address=data.data.data.officeName?data.data.data.officeName:''
        this.birthday=data.data.data.birthday?data.data.data.birthday.substring(0,10):''
      }else{
        this.$toast(data.data.msg)
      }
    })
  },
  methods:{
    closeModal(){
      this.show=false
      this.showDate=false
    },
    confirmFn(data){
      this.address=data.select1.text
      this.officeId=data.select1.value
      this.show=false
    },
    close(){
      this.show=false
    },
    register(){
      let _this=this
      if(!this.name){
        this.$toast('请输入您的姓名')
        return false
      }
      if(!this.getByteLen(this.name,50)){
        this.$toast('长度不能超过50个字符，中文算作2个字符')
        return false
      }
      if(!this.company){
        this.$toast('请输入您的公司名称')
        return false
      }
      if(!this.getByteLen(this.company,200)){
        this.$toast('长度不能超过200个字符，中文算作2个字符')
        return false
      }
      /*
      if(!this.position){
        this.$toast('请输入您的职位')
        return false
      }
      if(!this.getByteLen(this.position,50)){
        this.$toast('长度不能超过50个字符，中文算作2个字符')
        return false
      }
      if(!this.birthday){
        this.$toast('请选择您的生日')
        return false
      }
      */
      if(!this.address){
        this.$toast('请选择您的办事处')
        return false
      }
      this.getData('/web/member/info','patch',{
        'NickName':this.name,
        'CompanyName':this.company,
        'Position':this.position,
        'Birthday':`${this.birthday} 00:00:00`,
        'OfficeId':this.officeId,
      }).then(data=>{
        if(data.data.returnCode==200){
          if(_this.auditStatus==0){
            localStorage.setItem('auditStatus',false)
            this.$toast('提交成功，请等待审核')
          }else if(_this.auditStatus==-1){
            localStorage.setItem('auditStatus',false)
            this.$toast('抱歉，您的账号未通过审核')
          }else if(_this.auditStatus==1){

            localStorage.setItem('auditStatus',true)
            this.$router.push('/account')
          }
        }else{
          this.$toast(data.data.msg)
        }
    })
    },
    onFocus(){
      this.show=true;//展示办事处
      this.showDate=false
    },
    openDate(){
      this.show=false
      this.showDate=true;//展示生日
    },
    onblur(){
      this.show=false;
    },
    dateConfirm(){
      let date=this.time.getTime()
      if(date>0){
        this.birthday=utils.dateFormatFun(this.time.getTime(),1)
      }else{
        let year=this.time.getFullYear()
        let month=this.time.getMonth()+1
        let day=this.time.getDate()
        if(month<10){
          month='0'+month
        }
        if(day<10){
          day='0'+day
        }
        this.birthday=`${year}-${month}-${day}`
      }
      this.showDate=false;
    },
    dateCancel(){
      this.showDate=false;//隐藏生日展示
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.main{
  height: 100%;
  background: url('../assets/images/bg@2x.png') no-repeat left top;
  background-size: 100% 100%;
  .logo{
    width:1.425rem;
    // height: 0.53rem;
    padding-top: 1.08rem;
    margin: 0 auto 0.45rem;
    font-size: 0;
    img{
      width: 100%;
      height:100%;
    }
  }
  section{
    margin-bottom: 0.415rem;
  }
  .tzh-input{
    height: 0.52rem;
    padding: 0 0.165rem 0 0.56rem;
    font-size: 0.14rem;
    line-height: 0.52rem;
    &.name-input{
      background: url('../assets/images/name@2x.png') no-repeat 0.215rem center;
      background-size: 0.18rem 0.18rem;
    }
    &.company-input{
      background: url('../assets/images/company@2x.png') no-repeat 0.215rem center;
      background-size: 0.18rem 0.18rem;
    }
    &.position-input{
      background: url('../assets/images/position@2x.png') no-repeat 0.215rem center;
      background-size: 0.145rem 0.195rem;
    }
    &.birthday-input{
      background: url('../assets/images/birthday@2x.png') no-repeat 0.215rem center;
      background-size: 0.18rem 0.18rem;
    }
    &.address-input{
      background: url('../assets/images/address@2x.png') no-repeat 0.215rem center;
      background-size: 0.145rem 0.225rem;
    }
    input{
      background: none;
      width: 100%;
      color: #fff;
      opacity: 0.5;
    }
  }
  .mint-button--primary{
    background-image: linear-gradient(-168deg, #1786FF 0%, #33BBFF 100%);
  }
  .mint-button--large{
    background-image: linear-gradient(-168deg, #1786FF 0%, #33BBFF 100%);
    margin: 0 auto;
    width: 3.43rem;
    height: 0.45rem;
      .mint-button-text{
        font-size: 0.18rem;
        letter-spacing: 6px;
      }
  }
  .toLogin{
    font-size: 0.14rem;
    color: #fff;
    text-align: center;
    line-height: 0.51rem;
    a{
      color: #fff;
      text-decoration: underline;
    }
  }
}
  .mint-popup-bottom{
    width:100%;
  }

</style>



