<template>
  <div>
    <Header title="任务" :back="false"/>
    <ul class="task">
      <li class="border" v-for="(it,index) in info">
        <router-link to="taskDetail">
          <!--<h3>夺宝商城</h3>-->
          <h3>{{it.title}}</h3>
          <p class="p1">2019邮箱喊你开工啦，快来看看你的开工标签！</p>
          <p class="p2 flex_space">
            <!--<span class="word flex1">基督教啊啥的就啊撒大家多哈接电话就撒娇大手机多</span>-->
            <span class="word flex1">{{it.detail}}</span>
            <!--<span>2019-3-16</span>-->
            <span>{{it.addtime}}</span>
          </p>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'
export default {
  name: 'Task', 
  components: {
    Header
  },
  data () {
    return {    
    	info:''
    }
  },
  mounted () {
		var that=this
		axios({
      	url:"http://yg.91chihu.com/api.php/notice/gettask",
      })/*.then(response => {
      	(this.info = response.data.buyingduobao)
      	console.log(response.data.message)
      	console.log(response.data.buyingduobao)
      	}
     )*/
    .then(/*function(result){
    //	console.log(result)
    	var infoo=result.data.buyingduobao
			console.log(infoo)
    }*/
		   function(result){
					    //	console.log(result)
					    	that.info=result.data
								console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .task{
    width:100%;
    background: #fff;
    li{
      width: 100%;
      a{
        width:100%;
        display: block;
        padding: 0.2rem 0.24rem;
      }
      p{
        text-align: left;
        font-size: 0.22rem;
        color:#222;
        line-height: 0.44rem;
      }
      .p2{
        span{
          color: #666;
          &:nth-of-type(1){
             width:70%;
          }
        }
      }
      h3{
        text-align: left;
        line-height: 0.50rem;
      }
    }
  }
</style>



