<template>
  <div id="app">
    <router-view/>
    <Footer v-if="footerShow"/>
  </div>
</template>

<script>
import Footer  from '@/components/Footer'
export default {
  name: 'App',
  data(){
    return {      
      footerShow:false,
       showPath:['/','/home','/task','/share','/mine'] //需要显示底部的页面
    }
  },
  mounted(){
    if (this.showPath.indexOf(this.$route.path) >= 0) {
      // 根据不同页面需要，隐藏显示底部footer
      this.footerShow = true;
    }else{
      this.footerShow = false;
    }    
  },
  watch: {   //路由变化时改变footerShow
    $route (to, from) {
      let toDepth = to.path.split('/').length
      let fromDepth = from.path.split('/').length
      let to_flag = 0;
      let from_flag = 0;
      sessionStorage.setItem('routeToName', to.name);
      sessionStorage.setItem('routeFromName', from.name);
      if (this.showPath.indexOf(to.path) >= 0) {
        // 根据不同页面需要，隐藏显示底部footer
        this.footerShow = true;
        to_flag = 1
      } else {
        this.footerShow = false;
      }
      if (this.showPath.indexOf(from.path) >= 0) {
        from_flag = 1
      }
    }
  },
  components:{Footer}
}
</script>

<style>
body{
  background: #f4f4f4;
  width: 100%;
  overflow-x: hidden;
}
#app{
  height:100%;
  position:relative;
}
</style>



