<template>
  <div>
    <footer>
      <van-tabbar v-model="active">
        <van-tabbar-item @click="routerTo(1)">
          <span>首页</span>
          <i class="iconfont" :class="props.active?'home':'home_on'" slot="icon" slot-scope="props"></i>
        </van-tabbar-item>
        <van-tabbar-item @click="routerTo(2)">
          <span>任务</span>
          <i class="iconfont" :class="props.active?'task':'task_on'" slot="icon" slot-scope="props"></i>
        </van-tabbar-item>
        <van-tabbar-item @click="routerTo(3)">
          <span>分享</span>
          <i class="iconfont" :class="props.active?'share':'share_on'" slot="icon" slot-scope="props"></i>
        </van-tabbar-item>
        <van-tabbar-item @click="routerTo(4)">
          <span>我的</span>
          <i class="iconfont" :class="props.active?'mine':'mine_on'" slot="icon" slot-scope="props"></i>
        </van-tabbar-item>
      </van-tabbar>
    </footer>   
  </div>
</template>
<script>
import Vue from 'vue'
import { Tabbar, TabbarItem } from 'vant'
Vue.use(Tabbar).use(TabbarItem)

export default {
  name: 'Footer',
  data () {
    return {
      active: 0     
    }
  },
  mounted(){
    if(this.$route.path=='/'||this.$route.path=='/home'){
      this.active=0
    }
    if(this.$route.path=='/task'){
      this.active=1
    }
    if(this.$route.path=='/share'){
      this.active=2
    }
    if(this.$route.path=='/mine'){
      this.active=3
    }
  },
  watch:{
    $route (to, from) {
      if(to.path=='/'||to.path=='/home'){
        this.active=0
      }
      if(to.path=='/task'){
        this.active=1
      }
      if(to.path=='/share'){
        this.active=2
      }
      if(to.path=='/mine'){
        this.active=3
      }
    }
  },
  methods:{
    routerTo (num) {
      if(num==1){
        this.$router.push({path: '/'})
      }else if(num==2){
        this.$router.push({path: './task'})
      }else if(num==3){
        this.$router.push({path: './share'})
      }else if(num==4){
        this.$router.push({path: './mine'})
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

  footer{
    width: 100%;
    height: 1rem;
    border-top: 1px solid #ddd;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 500;
    .van-tabbar-item--active{
      color: #fa2f26;
    }
    .iconfont{
      width: 0.4rem;
      height: 0.4rem;
      display: block;
    }
    .home{
      background: url('~@/assets/dcfws.png') no-repeat center;
      background-size: auto 100%;
    }
    .home_on{
      background: url('~@/assets/sdcvb.png') no-repeat center;
      background-size: auto 100%;
    }
    .task{
      background: url('~@/assets/esxdf.png') no-repeat center;
      background-size: auto 100%;
    }
    .task_on{
      background: url('~@/assets/dcswwde.png') no-repeat center;
      background-size: auto 100%;
    }
    .share{
      background: url('~@/assets/ijuhg.png') no-repeat center;
      background-size: auto 100%;
    }
    .share_on{
      background: url('~@/assets/edftg.png') no-repeat center;
      background-size: auto 100%;
    }
    .mine{
      background: url('~@/assets/xcfvg.png') no-repeat center;
      background-size: auto 100%;
    }
    .mine_on{
      background: url('~@/assets/edfr.png') no-repeat center;
      background-size: auto 100%;
    }
  }
</style>



