//引入fetch.js文件
import { fetch ,fetchAdmin} from './fetch'
//定义获取数据的函数getData(),其中url,type,data对应fetch(config)中的config
export  function getData(url,type,data) {
//如果type为空，默认为post方法
    if(type===''){
      type = 'post';
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='post'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='patch'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='delete'){
      return fetch({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='get'){
      return fetch({
        url: url,
        method: type,
        params: data
      })
    }
}


export  function getAdminData(url,type,data) {
//如果type为空，默认为post方法
    if(type===''){
      type = 'post';
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='post'){
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='patch'){
      return fetchAdmin({
        url: url,
        method: type,
        data: data
      })
    }
    if(type=='get'){
      return fetchAdmin({
        url: url,
        method: type,
        params: data
      })
    }
}



