<template>
  <div>
    <Header title="晒单分享"/>
    <ul class="shareUl">
      <li  v-for="(item,index) in info" :key="index">
        <div class="top flex">
          <div class="bigImg">
            <img :src="item.headimg" alt="">
          </div>
          <div class="right flex1">
            <p class="p1 flex_space">
              <span>{{item.nickname}}</span>
              <span class="time">{{item.addtime}}</span>
            </p>
            <p class="p2">
              <b>[第{{item.good_no}}期]</b>
              <span>{{item.good_name}}</span>
            </p>
          </div>
        </div>
        <div class="content">{{item.comment}}</div>
        <div class="imgUl flex_wrap">
          <div class="imgli">
            <img :src="item.good_pic" @click="showImagePreview(item)" alt=" ">
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { ImagePreview } from 'vant'
import axios from 'axios'

export default {
  name: 'Home',
  components:{
    Header
  },
  data () {
    return { 
     show: true,
     images: [
       ''
      ],
      info:'',
      imgs: []
    }
  },
  mounted () {
   var that =this
  	axios({
      	url:"http://yg.91chihu.com/api.php/duobao/showwin",
      	method:"get",
      })
    .then(
		   function(result){
					    	that.info=result.data
					    	console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    showImagePreview(item) {
    	 console.log(this.images)
    	  this.imgs = []
    	  for(let i = 0; i < this.images.length; i++) {
            this.imgs.push(this.images[i].good_pic)
            
						this.imgs = []
						this.imgs.push(item.good_pic)
            
            console.log(this.images[i])
        }
    	  ImagePreview(this.imgs)
    	  console.log(this.imgs)
    }
  }
}
</script>


<style lang="scss" scoped>
  .van-image-preview {
    img {
      -webkit-user-drag: none;
    }
  }
  .shareUl{
    width: 100%;
    background: #fff;
    padding:0 0.3rem;
    li{
      padding: 0.3rem 0;
      border-bottom: 1px solid #eee;
      &:last-child{
        border: none;
      }
      .top{
        width:100%;
        .bigImg{
          width: 0.8rem;
          height: 0.8rem;
          border-radius: 0.1rem;
          margin-right: 0.2rem;
          overflow: hidden;
        }
        .right{
          p{
            width: 100%;
          }
          .p1{
            span{
              font-size: 0.30rem;
              color: #333;
              line-height: 0.44rem;
            }
            .time{
              font-size: 0.28rem;
              color: #666;
            }
          }
          .p2{            
            b{
              color: #e64535;
              font-size: 0.26rem;
            }
            span{
              color: #999;
              font-size: 0.26rem;
            }
          }
        }
      }
      .content{
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.44rem;
        padding: 0.3rem 0;
        color: #333;
      }
      .imgUl{
        width: 100%;
        .imgli{
          width: 1.1rem;
          height:  1.2rem;
          overflow: hidden;
          margin-right: 0.2rem;
          margin-bottom: 0.2rem;
        }
      }
    }
  }
</style>





