<template>
  <div>
    <Header title="实名认证" />
    <div class="atteatation">
      <div class="main">
        <div class="topIco"></div>
        <div class="atteatInput">
          <van-cell-group>
            <van-field
              v-model="phone"
              required
              clearable
              label="手机号"
              right-icon="question-o"
              placeholder="请填写手机号"
            />
           <van-field
              v-model="sms"
              center
              required
              clearable
              label="验证码"
              placeholder="请输入验证码"
            >
            
              <van-button slot="button" size="small" type="primary" @click="getMsg" v-model="verifyCodeTips">{{verifyCodeTips}}</van-button>
              
            </van-field>
            <van-field
              v-model="name"
              required
              clearable
              label="姓名"
              right-icon="question-o"
              placeholder="请填写姓名"
            />
            <van-field
              v-model="idCard"
              required
              clearable
              label="身份证"
              right-icon="question-o"
              placeholder="请填写身份证"
            />
          </van-cell-group>
          <div class="uploadBox">
            <h3><i>*</i>身份证正面</h3>
            <ul class="uploadUl flex">
              <li class="flex1 border-right">
                <div class="bigImg flex_row">
                   <!--  <img src="~@/assets/uhdgf.png" alt="" > -->
                   <img :src="idcard.front" alt="" >  
                   <!--  显示图片 没有的时候 显示初始值 -->
                </div>
              </li>
              <li class="flex1">
                <van-uploader :after-read="onRead"></van-uploader> 
                <!--  上传图片操作 既然是两个共用一个方法 必须做区分 -->
              </li>
            </ul> 
            <p>#请对身份证正面拍摄，身份证完全入境，身份证号码清晰可见#</p>
          </div>
          <div class="uploadBox">
            <h3><i>*</i>身份证反面</h3>
            <ul class="uploadUl flex">
              <li class="flex1 border-right">
                <div class="bigImg flex_row">
                   <!--<img src="~@/assets/okjd.png" alt="">-->
                  <img :src="idcard.back" alt="" >  
                  <!--  显示图片 没有的时候 显示初始值 -->
                </div>
              </li>
              <li class="flex1">
                <van-uploader :after-read="onRead1"></van-uploader>  
                <!--  上传图片操作 -->
              </li>
            </ul> 
            <p>#请对身份证反面拍摄，证件信息清晰可见，身份证过期则无效#</p>
          </div>
          <div class="btmBtn" @click="submit">
            <van-button :disabled="false" type="danger"><span>确认提交</span></van-button>  
            <!--   提交上去所有信息 身份证图片的后台交互的路径也要协同上传 -->
          </div>
        </div>
      </div>
    </div>    
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'


export default {
  name: 'Mine', 
  components: {
    Header
  },
  data () {
    return { 
    		info:'',
    	 	phone:'',
    	 	sms:'',
    	 	idCard:'',
    	 	name:'',   
    	 	idcard_pic1:'', // 你写的图片"~@/assets/uhdgf.png"
    	 	idcard_pic2:'', // 你写的图片
    	 	 countdown: 60,
    	 	 verifyCodeTips: '获取验证码',
    	 	 codeEnable: true,
    	 	 thumb: '',
    	 	 type: '',
			idcard: {
				'front':require('@/assets/uhdgf.png'),
				'back': require('@/assets/okjd.png')// 上面两个图片也要用这个
			}
    }
  },
  
  mounted () {
  	//console.log(this.$route.params.id)
  },

  methods:{
  	  getMsg(){
  	//alert("验证码已发送请注意查收")
  	 if(!this.codeEnable){
      			return
      }
  	 if(!(/^1[3456789]\d{9}$/.test(this.phone))){ 
        alert("手机号码有误，请重填");  
        return; 
    	} 
    	 axios({
      	url:"http://192.168.1.192/duobao/api.php/public/sendmessage",
      	method:"post",
      	params:{
						mobile:this.phone
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	  console.log(response.data.member)
      	  this.settime();
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
        console.log(mobile)
      });
     
     },
  	settime:function () {
        var that = this
        if (that.countdown == 1) {
            that.countdown = 60;
            that.verifyCodeTips = "获取验证码";//没用
            that.codeEnable = true;
            return;
        } else {
            that.countdown--;
            that.verifyCodeTips = "重新获取(" + that.countdown + ")";
            that.codeEnable = false
        }
        setTimeout(function(){
            that.settime();
            
        }, 1000)
    },
  	
    onRead(file) {
	  //console.log(str) // 看下str能不能打印出来 能打印的话 后面才能用
     // console.log(file) // file对象 文件专用ajax上传对象
     // console.log(event)
	  let fromdata = new FormData() // 新建fromdata对象
	  let that = this // this 静态化
	  fromdata.append('image', file.file) // 对象中添加file对象
      axios({
      	url:"http://192.168.1.192/duobao/api.php/public/uploadimg",  // 看有没有跨域问题 有的话 还得解决跨域问题
      	method:"post",
//    	params:{
////						idcard_pic1:null,/*身份证正面*/
////						idcard_pic2:null/*反面*/
//			// image: file
//			fromdata
//    	}
				headers:{"Content-type":"multipart/form-data"},
				data:fromdata
      }).then(response => {
      	 // (this.infosc = response.data)
		 // that.idcard[str] = response.map.image  // 返回值是图片的网络路径 寄存在data中的idcard对象中 后期总数据提交的时候也要用到 同时图片的显示需要用
		 that.idcard.front = response.data.image
		// that.idcard.back = response.data.image
		 console.log(response.data)
		// console.log(JSON.parse(response.data))
		// console.log(response.data.image)
		// 	console.log(response)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    },
     onRead1(file) {
	  //console.log(str) // 看下str能不能打印出来 能打印的话 后面才能用
     // console.log(file) // file对象 文件专用ajax上传对象
     // console.log(event)
	  let fromdata = new FormData() // 新建fromdata对象
	  let that = this // this 静态化
	  fromdata.append('image', file.file) // 对象中添加file对象
      axios({
      	url:"http://192.168.1.192/duobao/api.php/public/uploadimg",  // 看有没有跨域问题 有的话 还得解决跨域问题
      	method:"post",
//    	params:{
////						idcard_pic1:null,/*身份证正面*/
////						idcard_pic2:null/*反面*/
//			// image: file
//			fromdata
//    	}
				headers:{"Content-type":"multipart/form-data"},
				data:fromdata
      }).then(response => {
      	 // (this.infosc = response.data)
		 // that.idcard[str] = response.map.image  // 返回值是图片的网络路径 寄存在data中的idcard对象中 后期总数据提交的时候也要用到 同时图片的显示需要用
		// that.idcard.front = response.data.image
		 that.idcard.back = response.data.image
		 console.log(response.data)
		// console.log(JSON.parse(response.data))
		 console.log(response.data.image)
		 	console.log(response)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    },
    submit () {
	   const that = this // this 静态化
       axios({
      	url:"http://192.168.1.192/duobao/api.php/member/certification",
      	method:"post",
      	params:{
      			id:this.$route.params.id,
						mobile: this.phone, /*手机号data.username:*/
						//mobile:data.sms, /*验证码data.sms:(用的是this.sms 是人所输入的值 不是后台发过来的值 )  */
						realname: this.name, /*真实姓名data.name:*/
						verify:this.sms,/*验证码*/
						idcard: this.idCard, /*身份证号*/
						idcard_pic1: that.idcard.front, /*身份证正面*/
						idcard_pic2: that.idcard.back /*反面*/
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },

    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .atteatation{
    padding: 0.7rem 0.3rem 0.3rem;
    .van-button--primary{
      background: none;
      border: 1px solid #e64535;
      border-radius: 0.5rem;      
      span.van-button__text{
        color: #e64535!important;       
      }
    }
    .main{
      background: #fff;
      width: 100%;
      min-height: 3.0rem;
      border-radius: 0.15rem;
      box-shadow: 0 0 0.15rem #ddd;
      position: relative;
      .topIco{
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        text-align: center;
        top: -0.42rem;
        width: 0.72rem;
        height: 0.72rem;
        border-radius: 0.72rem;
        border: 0.04rem solid #fff;
        background: url('~@/assets/cvbgh.png') no-repeat center;
        background-size: 100% auto;
      }
      .atteatInput{
        padding: .44rem .4rem;
        .van-field .van-cell__title{
          max-width: 0.7rem
        }
        .uploadBox{
          width: 100%;
          h3{
            padding-left: 0.1rem;
            font-size: 0.3rem;
            line-height: 0.8rem;
            padding-top: 0.2rem;
            i{
              color: #fd4040;
            }
          }
          p{
            color: #fd4040;
            font-size: 0.24rem;
            line-height: 0.44rem;
          }
          .uploadUl{
            width:100%;
            height: 2.75rem;
            background: #f5f5f5;
            border: 1px solid #dedede;
            border-radius: 0.1rem;
            li{
              background: url('~@/assets/jnhdgf.png') no-repeat center;
              background-size: 1.12rem auto;
              .van-uploader{
                display: block;
                width: 100%;
                height: 100%;
              }
              .bigImg{
                width: 100%;
                height: 100%;
                background: #fff;
                img{
                  max-width: 1.7rem;
                  height: 1.1rem;                  
                }
              }
            }
          }
        }
        .btmBtn{
          width: 100%;
          height: 0.88rem;
          margin-top: 0.5rem;         
          button{
            width: 100%;
            height: 100%;
            color: #fff;
            span{
              color: #fff;
              font-size: 0.3rem; 
            }
          }
        }
      }
    }
  }
</style>



