<template>
  <div> 
    <Header title="关于平台" />   
    <div class="content" align="center">
      <!--<p>我们的平台是关于互联网方面的全面性的功能行的平台
        具有很全面的各种设备的全面性功能性平台，关于使用
        上的注意事项有一下几点：</p>-->
        <p>{{info.title}}</p>
      <!--<p>
        1.的黄金时间活动结束的时间；
        2.就能承受的护卫三叉神经得分后卫手机打不；
        3.面包车加速度，是堆积物饿还是坚持呢；
        4.就是粗的活动，是就电话为的华师大次肯德基猝死上端茶倒水。
      </p>-->
      <p>
        {{info.detail}}
      </p>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'About',
  components: {
    Header
  },
  data () {
    return {
    	info:''
    }
  },
  mounted () {
		var that=this
		axios({
      	url:"http://192.168.1.192/duobao/api.php/system/about",
      })/*.then(response => {
      	(this.info = response.data.buyingduobao)
      	console.log(response.data.message)
      	console.log(response.data.buyingduobao)
      	}
     )*/
    .then(/*function(result){
    //	console.log(result)
    	var infoo=result.data.buyingduobao
			console.log(infoo)
    }*/
		   function(result){
					    //	console.log(result)
					    	that.info=result.data
								console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .content{
    line-height: 0.44rem;
    padding: 0.3rem;
    min-height: 100vh;
    width:100%;
    background: #fff;
  }
</style>



