      <div class="homeRt flex_col">
        <i class="icon"></i>
        <span>消息</span>
      </div>
    </div>

    <van-swipe :autoplay="3000" :show-indicators="false">
      <van-swipe-item v-for="(image, index) in 3" :key="index">
