  methods: {
    toback () {
      this.$router.push({ name: 'Home'})
    }    
  }
}
</script>


<style lang="scss" scoped>
  .demo-dialog {
    .van-doc-demo-block > .van-button {
      margin-left: 15px;
    }
  }
  .van-swipe{
    width:100%;
    height: 3.62rem;
  }
  .btmImage{
    padding: 0.2rem 0.2rem 0;
  } 
  .header{
    width: 100%;    
    padding: 0.3rem;
    .title{
      font-size: 0.32rem;
    }
    .icnofont{
      width: 0.3rem;
      height: 0.3rem;
      background: url('~@/assets/dcfrg.png');
      background-size: 100% auto;
    }
  }
  .main{
    width: 100%;
    padding: 0.36rem 0 0.9rem;
    .bigimg{  
      &:nth-of-type(1){
        padding-right: 1.2rem;
      }
      img{
        width: 0.89rem;
        height: 0.89rem;
      }
      span{
        font-size: 0.30rem;
        color: #333;
        padding-top: 0.15rem;
      }
    }
  }
</style>





