<template>
  <div>
    <Header title="往期揭晓"/>
    <template>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        ><!--@load="onLoad"-->
      
        <ul class="shopBox padd">
          <li class="shoplis award flex" v-if="this.st!=1">
            <!--<div class="topLeft"></div>-->
            
           <div  class="topLeft"></div>
            
            <div class="bigImg">
              <!--<img src="~@/assets/edcgbh.png" alt="">-->
              	<img :src="info.fund_pic" alt="">
            </div>
            <div class="rightShop flex1">
              <!--<div class="shopTit word_wrap">
                （第{{info.good_no}}期）
              </div>-->
              <div class="titPast word_wrap">
                <!--Apple iPhone X (A1865) 64GB 深空灰色 移动联通电信4G手机-->{{info.fund_name}}
              </div>
              <div class="pirce">
                <span>价值： <i><!--8260元-->{{info.summoney}}</i></span>
              </div>
              <div v-if="this.st!=1" class="pasBtn">
                <van-button class="">
                  <em>揭晓倒计时：{{timestr}}</em><!--03:20:52-->
                </van-button>
              </div>
            </div>
          </li>
          <li class="shoplis flex" v-for="infoxias in infoxia">
            <div class="bigImg">
              <!--<img src="~@/assets/edcgbh.png" alt="">-->
              	<img :src="infoxias.fund_pic" alt="">
            </div>
            <div class="rightShop flex1">
              <div class="topRt flex">
                <div class="topRtimg">
                  <!--<img src="~@/assets/okjdh.png" alt="">-->
                  	<img :src="infoxias.headimg" alt="">
                </div>
                <div class="topText flex1">
                  <p class="p1">获得者：<!--137****4625-->{{infoxias.mobile}}</p>
                  <p class="p2">参与次数：<i>{{infoxias.count}}</i></p>
                </div>
              </div>
              <div class="pirce code">
                <span>幸运码： <i><!--1011002-->{{infoxias.winnumber}}</i></span>
              </div>
              <div class="timeline">揭晓时间：<!--2018-02-20 16:21:28.522-->{{infoxias.wintime}}</div>
            </div>
          </li>
        </ul>
      </van-list>
    </template>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return { 
      active: 0,
      nav:[
        {title: '正在抢购', id: 1},
        {title: '已经结束', id: 2}
      ],
      list: [],
      loading: false,
      finished: false,
      info:'',
      infoxia:'',
      st:'',
      
      		isEnd:false,//倒计时是否结束
        endTime:'',//应为接口获取到的结束时间 that.endtime = xxx.time
      time:{},
      timestr:''
    }
  },
  created(){
		let that = this;
		//that.setEndTime();
	},
  mounted () {
  	this.st=this.$route.query.status
		var that =this
  	axios({
      	url:"http://192.168.1.192/duobao/api.php/fund/pastshow",
      	method:"get",
      	params:{
      		id:this.$route.query.id
      	}
      })
    .then(
		   function(result){
					    	that.info=result.data.nowfund
					    	that.infoxia=result.data.pastshow
					    	that.endTime = that.info.endtime
					    	
					    	that.setEndTime();
					    	
					   // 	console.log(that.endTime)
					    /*	console.log(that.info.endtime)
					    	console.log(result.data.nowduobao.endtime)*/
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    tabHead (index,title) {
      console.log(index,title)
    },
    //（可能bug存在区）
    setEndTime(){
		var that = this;
		var interval = setInterval(function timestampToTime(){
		//var date = (new Date(that.endTime)) - (new Date()); //计算剩余的毫秒数
		var date = that.endTime *1000 - new Date().getTime()
		
	//	console.log(date)
		
		if(date == 0){
			that.isEnd = true;
			clearInterval(interval)
		}else{
			that.time.D = parseInt(date / 1000 / 60 / 60 / 24 , 10);
			that.time.h = parseInt(date / 1000 / 60 / 60 % 24 , 10);
			if(that.time.h < 10){
				 that.time.h = "0" +　that.time.h
			}
			that.time.m = parseInt(date / 1000 / 60 % 60, 10);//计算剩余的分钟
			if(that.time.m < 10){
				 that.time.m = "0" +　that.time.m
			} 
	  		that.time.s = parseInt(date / 1000 % 60, 10);//计算剩余的秒数 
	  		if(that.time.s < 10){
				 that.time.s = "0" +　that.time.s
			}
			//时分秒
			//that.timestr = that.time.D > 0? (that.time.D * 24 + parseInt(that.time.h)) + ':' +that.time.m  +  ':' +that.time.s : that.time.h + ':' +that.time.m  +  ':' +that.time.s;
			//天时分秒
				that.timestr =	that.time.D  + ':' +  that.time.h + ':' +that.time.m  +  ':' +that.time.s
		}
	},1000);
},
    
  }
}
</script>

<style lang="scss" scoped> 
  .shopBox{
    background: #fff;
    padding-top: 0.3rem;
  }
  .award{
    border: 1px solid #e64535;
    border-radius: 0.1rem;
    position: relative;
    .topLeft{
      width: 1.23rem;
      height: 1.18rem;
      position: absolute;
      left: 0;
      top: 0;
      background: url('~@/assets/award.png') no-repeat left top;
      background-size: 100% auto;
      z-index: 100;
    }
  }
  
  .shoplis{
    margin-top: 0.3rem;   
    padding: 0.4rem .3rem;
    box-shadow: 0 0 0.15rem #ddd;
    &:first-child{
      margin-top: 0;
    }
    .bigImg{
      width: 2.2rem;
      height: 2.6rem;
      border: 1px solid #eee;
      border-radius: 0.1rem;
      margin-right: 0.2rem;
      z-index: 50;
    }
    .rightShop{
      height: 100%;
      .topRt{
        width: 100%;
        .topRtimg{
          width: 0.98rem;
          height: 0.98rem;
          border-radius: 0.1rem;
        }
        .topText{
          padding-left: 0.15rem;
          p{
            font-size: 0.26rem;
            color: #333;
            line-height: 0.44rem;
            i{
              color: #e64535;
            }
          }
        }
      }
      .timeline{
        font-size: 0.24rem;
        line-height: 0.44rem;
        color: #666;
      }
      .shopTit{
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.4rem;
        color: #000;
      }
      .titPast{
        font-size: 0.26rem;
        color: #333;
        line-height: 0.4rem;
        padding-top: 0.08rem;
      }
      .pirce{
        padding: 0.08rem 0;
        span{
          font-size: 0.26rem;
          color: #666;
          i{
            color: #ffc944;
          }
        }
      }
      .code{
        padding: 0.15rem 0;
      }
      .van-button{
        width: 100%;
        margin-top: 0.2rem;
        height: 0.6rem;
        line-height: 0.6rem; 
        background: #e64535;
        em{
          display: block;
          color: #fff;
          height: 100%;
          font-style: normal;
          font-size: 0.24rem;
          background: url('~@/assets/defcv.png') no-repeat left center;
          background-size: 0.28rem auto;
        }
      }
      
    }
  }
</style>



