<template>
  <div>
    <Header title="收货地址" />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"         
      @add="onAdd"
      @edit="onEdit"
      add-button-text="新增地址"
    />
    
    
  </div>
</template>
<script>
import Header from '@/components/Header'
export default {
  name: 'PayMents', 
  components: {
    Header
  },
  data () {
    return { 
      chosenAddressId: '1',
      list: [
        {
          id: '1',
          name: '张三',
          tel: '13000000000',
          address: '浙江省杭州市西湖区文三路 138 号东方通信大厦 7 楼 501 室'
        },
        {
          id: '2',
          name: '李四',
          tel: '1310000000',
          address: '浙江省杭州市拱墅区莫干山路 50 号'
        }
      ]      
    }
  },
  mounted () {

  },
  methods: {
    onAdd() {
      this.$router.push({ name: 'NewAddress'})
      // Toast('新增地址');
    },

    onEdit(item, index) {
      this.$router.push({ name: 'NewAddress'})
      // Toast('编辑地址:' + index);
    }
  }
}
</script>
<style lang="scss" scoped> 
  .van-address-list__add{
    width: 100%;
  }
  
</style>



