<template>
  <div>
    <Header title="我的参与"/>
    <van-tabs
      v-model="active" 
      swipeable 
      @change="tabHead"
    >
      <van-tab  
        v-for="(it,index) in nav"
        :key="index"
        :title="it.title"
      >
        <template>
          <van-list><!--v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"-->
            <ul class="shopBox padd" v-for="(item,index) in info" :key="index">
              <li class="shoplis flex">
                <div class="bigImg">
                  <img :src="item.good_pic" alt="">
                </div>
                <div class="rightShop flex1">
                  <div class="shopTit word_wrap">
                    {{item.good_name}}<!--Apple iPhone X (A1865) 64GB 深空灰色 移动联通电信4G手机-->  
                  </div>
                  <p class="p1">商品期数:{{item.good_no}}</p>
                  <p class="p1">时间：<!--2019-02-6-->{{item.time}}</p>
                </div>
              </li>
            </ul>
          </van-list>
        </template>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return { 
      active: 0,
      info:'',
      nav:[
        {title: '未开奖', id: 1},
        {title: '未中奖', id: 2},
        {title: '中奖', id: 3}
      ],
      list: [],
      loading: false,
      finished: false,
    }
  },
  mounted () {
  //	var that=this
      var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myduobao",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:0
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.duobaos
					    	console.log(result)
								console.log(result.data.duobaos)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      
      
      
  },
  methods: {
    tabHead (index,title) {
      console.log(index,title)
      if(index==0){
      	//alert(title)
      	var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myduobao",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:0
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.duobaos
					    	console.log(result)
								console.log(result.data.duobaos)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }else if(index==1){
      //	alert(title)
      	var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myduobao",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:1
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.duobaos
					    	console.log(result)
								console.log(result.data.duobaos)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }else if(index==2){
      //	alert(title)
      	var that=this
      	axios({
      	url:"http://yg.91chihu.com/api.php/member/myduobao",
      	method:"get",
      	params:{
      		id:this.$route.params.id,
      		type:2
      	}
	     }).then(/*response => {
	      	(this.info = response.data.member)
	      	//console.log(this.info)
	      	console.log(response)
	      	}*/
	      	function(result){
					    //	console.log(result)
					    	that.info=result.data.duobaos
					    	console.log(result)
								console.log(result.data.duobaos)
    }
	     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  
  .shoplis{
    background: #fff;
    margin-top: 0.3rem;
    padding: 0.4rem .3rem;
    box-shadow: 0 0 0.1rem #ddd;
    .bigImg{
      width: 1.65rem;
      height: 1.65rem;
      border-radius: .1rem;
      margin-right: 0.2rem;
      border: 1px solid #eee;
    }
    .rightShop{
      height: 100%;
      .shopTit{
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.5rem;
        color: #e64535;
      }
      .p1{
        font-size: 0.26rem;
        color: #666;       
        line-height: 0.44rem;
        padding-top: 0.3rem;
      }    
    }
  }
</style>



