<template>
  <div>
    <Header title="充值"/>
    <div class="headImg">
      <img src="~@/assets/dcvgr.png" alt="">
    </div>
    <div class="rechargeMain padd">
      <div class="balanceBox flex_space">
        <div class="balance">
          <p>余额（元）</p>
          <p class="money">100.00</p>
        </div>
        <div class="inputMoney flex_row">
          <van-field v-model="money"  placeholder="请输入充值金额" />
          <span>元</span>
        </div>
      </div>
      <ul class="moneyUl flex_wrap">
        <li :class="{'checked':flag==index}" v-for="(it,index) in data" @click="tableVan(it, index)">
          <van-button class="moneyBtn" :type="flag==index?'danger':'default'" plain>
            <span>{{ it.value }}</span>
          </van-button>
        </li>
      </ul>
      <div class="chosePay charge">
        <div class="payTit">选择支付方式</div>
        <van-radio-group v-model="radio">
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/duhgs.png"/>
            <van-cell title="支付宝" clickable @click="radio = 1">
              <van-radio name="1" />
            </van-cell>          
          </van-cell-group>
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/sxcdk.png"/>
            <van-cell title="微信" clickable @click="radio = 2">
              <van-radio name="2" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
    </div>
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row" @click="toRecharge">
        <span>去充值</span>
      </van-button>
    </div>
  </div>
</template>
<script>
	
import Header from '@/components/Header'
import axios from 'axios'


export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return {
      data:[
        { value: '200元', id: 0 },
        { value: '100元', id: 1 },
        { value: '50元', id: 2 },
        { value: '20元', id: 3 },
        { value: '其他金额', id: 4 }
      ],
      money:this.money,
      flag: 0,
      radio: 0
    }
  },
  mounted () {
  	//console.log(money)
      console.log(this.money)
  },
  methods: {
    tableVan (it, index) {     
      this.flag = index
      this.money= it.value.replace('元', '')/*it.value.replace(/元/, '')都可以*/
    },
    toRecharge () {
      /*this.$router.push({ 
        name: 'Toast',
        params:{ type: 2 }
      })*/
     axios({
      	url:"http://yg.91chihu.com/api.php/member/recharge",
      	method:"post",
      	params:{
      			id:this.$route.params.id,
						changenum:this.money
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 

  .headImg{
    width: 100%;
    height: 4.07rem;
    background: #fff;
  }
  .rechargeMain{
    width: 100%;
    background: #fff;
    .balanceBox{
      width: 100%;
      height: 1.6rem;
      .balance{
        p:nth-of-type(1){
          font-size: 0.26rem;
          color: #999;
        }
        p:nth-of-type(2){
          color: #e64535;
          font-size: 0.28rem;
          font-weight: bold;
          padding-top: 0.1rem;
        }
      }
      .inputMoney{
        width: 2.9rem;
        border-bottom: 1px solid #ddd;
      }
    }
    .moneyUl{
      width: 100%;
      li{
        width: 30%;
        height: 1.0rem;
        margin-bottom: 0.25rem;
        &:nth-of-type(2){
          margin: 0 5%;
        }
        &:nth-of-type(5){
          margin: 0 5%;
        }
        button{
          width: 100%;
          height: 100%;
          span{
            font-size: 0.3rem;
            color: #cfcece;
          }
        }
      }
      .checked{
        .moneyBtn{
          background: url('~@/assets/cbgedf.png') no-repeat center;
          background-size: 100% 100%;
          span{
            color: #e64535;
          }
        }
      }
    }
  }
  .btmBtn{
    width: 100%;
    height: 1.4rem;
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0rem;
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: 0.88rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
</style>



