<template>
  <div>
    <Header title="详情" />
    <van-swipe :autoplay="3000" indicator-color="#e64535">
      <van-swipe-item> <!--v-for="(image, index) in 3" :key="index"-->
        <img src="~@/assets/timg.jpg" />
      </van-swipe-item>
    </van-swipe>
    <div class="shopInfo">
      <h3>
        <span>{{ info.status==1? '已结束':'抢购中' }}</span>
        <!--Apple iPhone X (A1865) 64GB 深空灰色 移动联通电信4G手机-->{{info.fund_name}}
      </h3>
      <!--<div class="counts">期号：{{info.good_no}}</div>-->
      <div class="lineBox">
        <span class="line" style="width: 60%;"></span>
      </div>
      <div class="numberShop flex">
        <div class="numlis flex1 ">       
          <span>总需人数<!--2289-->{{info.neednumber}}人次</span>
        </div>
        <div class="numlis flex1 flex_row">
          <span>参与</span>
          <b><!--89-->{{info.joinnumber}}</b>
        </div>
        <div class="numlis flex1 ">          
          <span>剩余</span>
          <b><!--2200-->{{info.surplusnumber}}</b>
        </div>
      </div>
    </div>
    <div class="detLis">
      <div class="flex_space padd" @click="toDreamPastShop">
        <span>往期揭晓</span>
        <van-icon name="arrow" />
      </div>
      <router-link to="/DreamLookShare" class="flex_space padd">
        <span>晒单分享</span>
        <van-icon name="arrow" />
      </router-link>
    </div>
    <div class="btmBtn" @click="toDreamPayment">
     <van-button type="danger" :disabled = "info.status == '1'"><span>一元夺宝</span></van-button>
     <!--<van-button :class="{'gray':active==1}"><em>参与夺宝</em></van-button>-->
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  components: {
    Header
  },
  data () {
    return {      
    	info:''
    }
  },
  mounted () {
  //	console.log(this.$route.params.id)
		var that =this
  	axios({
      	url:"http://192.168.1.192/duobao/api.php/fund/funddetail",
      	method:"get",
      	params:{
      		id:this.$route.params.id
      	}
      })
    .then(
		   function(result){
					    //	console.log(result)
					    	that.info=result.data.fund
								console.log(result.data.fund)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    toDreamPayment () {
      //this.$router.push({ name: 'PayMents'})
       this.$router.push({
      	name: 'DreamPayMents',
      	query: {
            id:this.info.id
          }
      	})
    },
    
    toDreamPastShop(){
    	this.$router.push({
          path: '/dreampastShop', 
          query: {
            status: this.info.status,
            id:this.info.id
          }
        })
    }
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  .van-swipe{
    width:100%;
    height: 4.2rem;
  }
  .btmImage{
    padding: 0.2rem 0.2rem 0;
  } 
  .shopInfo{
    width: 100%;
    padding: 0.3rem;
    background: #fff;
    h3{
      font-size: 0.28rem;
      color:#000;
      line-height: 0.44rem;
      span{
        font-size: 0.24rem; 
        color: #fff;
        padding: 0.06rem 0.2rem;
        border-radius: 0.2rem;
        background: #e64535;
      }
    }
    .counts{
      font-size: 0.24rem;
      color: #666;
    }

  }
  .lineBox{
    width: 100%;
    margin: 0.2rem 0;
    height: 0.1rem;
    background: #ddd;
    border-radius: .1rem;
    position: relative;
    .line{
      height: .1rem;
      display: block;
      position: absolute;
      left: 0;
      top:0;
      background: #f5c243;
      border-radius: .1rem;
    }
  }
  .numberShop{
    width: 100%;
    .numlis{
      &:nth-of-type(2){
        b{
          color: #e64535;
        }
      }
      &:nth-of-type(3){
        b{
          color: #f1c53e;
        }
      }
      b{
        font-size: 0.26rem;
        color: #666;
      }
      span{
        font-size: 0.26rem;
        color: #666;
      }
    }
  }
  .detLis{
    width:100%;
    background: #fff;
    margin-top: 0.3rem;
    a{
      height: 0.88rem;
      border-bottom: 1px solid #ddd;
      &:last-child{
        border: none;
      }
      span{
        font-size: 0.28rem;
        color: #333;
      }    
    }
  }
  .btmBtn{
    width: 100%;
    height: 0.88rem;
    position: fixed;
    left: 0;
    bottom: 0;
    button{
      width: 100%;
      height: 100%;
      color: #fff;
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
</style>



