<template lang="html">
  <div>
    <div class="headers">
      <van-nav-bar
        :title="title"     
        :left-arrow="back"
        @click-left="onClickLeft"
      />
    </div>
  </div>
</template>

<script>

export default {
  props:{
    title: {
      type: String,
      default: null
    },
    back: {
      type: Boolean,
      default: true
    }
  },
  data(){
    return {
    }
  },
  mounted(){
    
  },
  methods:{
    onClickLeft() {
      this.$router.go(-1)
    }   
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .headers{
    width: 100%;
    height: 0.92rem;
    .van-nav-bar{
      width:100%;
      height:  0.92rem;
      position: fixed;
      left: 0;
      top: 0;
      background: #fff;
      z-index: 100!important;
    }
  }
</style>



