<template>
  <div>
    <Header title="消息详情" />
    <div class="content" v-for="(it,index) in info">
      <van-row>
        <van-col span="5">发布时间：</van-col>
        <van-col span="19" >{{it.addtime}}</van-col>
      </van-row>
      <van-row >
        <van-col span="5">任务详情：</van-col>
        <van-col span="19"> 
          <!--简洁的方式风刀霜剑副教授，时间的烦恼就
          是对方护肤时间。收到货速度湖床上绝代风
          华度粉红色的，对空射击舞蹈和死党但是解
          决司法为佛教圣地，手机导航无发的和是对
          电脑上看的--> {{it.detail}}
        </van-col>
      </van-row>

    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'
export default {
  name: 'Task', 
  components: {
    Header
  },
  data () {
    return {      
    	info:''
    }
  },
  mounted () {
		var that=this
		axios({
      	url:"http://yg.91chihu.com/api.php/notice/gettask",
      })/*.then(response => {
      	(this.info = response.data.buyingduobao)
      	console.log(response.data.message)
      	console.log(response.data.buyingduobao)
      	}
     )*/
    .then(/*function(result){
    //	console.log(result)
    	var infoo=result.data.buyingduobao
			console.log(infoo)
    }*/
		   function(result){
					    //	console.log(result)
					    	that.info=result.data
								console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  .content{
    width:100%;
    min-height: 100vh;
    background: #fff;
    padding: 0.3rem;
    .van-row{
      padding-bottom: 0.15rem;
    }
    .van-col{
      font-size: 0.28rem;
      color: #222;
      line-height: 0.44rem;
    }
  }
</style>



