<template>
  <div>
    <Header title="客服"/>
    <div class="customer padd">
      <van-swipe>
        <van-swipe-item>
          <img src="~@/assets/esxcde.png" alt="">
        </van-swipe-item>        
      </van-swipe>
      <h3 >工作时间：上午8:00 - 晚上9:00</h3>
      <div class="scanCode flex_col">
        <div class="bigImg">
          <img :src="info.weixincode" alt=""><!--{{info.weixincode}}-->
        </div>
        <p>微信：{{info.weixin}}</p>
      </div>
    </div>
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row">
        <van-icon name="phone" />
        <span>一键拨打</span>
      </van-button>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return {     
      info:''
    }
  },
  mounted () {
		var that=this
		axios({
      	url:"http://192.168.1.192/duobao/api.php/system/getkefuweixin",
      })/*.then(response => {
      	(this.info = response.data.buyingduobao)
      	console.log(response.data.message)
      	console.log(response.data.buyingduobao)
      	}
     )*/
    .then(/*function(result){
    //	console.log(result)
    	var infoo=result.data.buyingduobao
			console.log(infoo)
    }*/
		   function(result){
					    //	console.log(result)
					    	that.info=result.data
								console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  .customer{
    width:100%;
    height: 100%;
    background: #fff;
    h3{
      font-size: 0.28rem;
      color: #999;
      line-height: 0.74rem;
      text-indent: 0.2rem;
      border-bottom: 1px solid #ddd;
    }
  }
  .van-swipe{
    width:100%;
    height: 2.75rem;    
  }
  .scanCode{
    width: 100%;
    padding: 1.5rem 0 2.8rem;
    .bigImg{
      width: 2.5rem;
      height: 2.5rem;      
    }
    p{
      color: #000;
      padding-top: 0.3rem;
    }
  }
  .btmBtn{
    width: 100%;
    height: 2.15rem;
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0;
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: 0.88rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
</style>



