          </div>
          <b>{{info.nickname}}</b>
          <span><!--推广编号：GHYT87654-->{{info.mark}}</span>
          <div class="iconfont" @click="toInfo">
            <van-icon  info="10" />
          </div>
        </div>
        <ul class="mineBtm flex">
          <li class="flex1">
