          </router-link>
        </li>
        <li>
          <router-link :to="'geneCode/'+info.id" class="flex_col">
            <img src="~@/assets/edsc.png" alt="">
            <span>推广码</span>
          </router-link>
        </li>
        <li >
          <router-link to="/customer" class="flex_col">
            <img src="~@/assets/rfd.png" alt="">
            <span>客服</span>
          </router-link>
        </li>
        <li >
          <router-link to="about" class="flex_col">
            <img src="~@/assets/edcvg.png" alt="">
            <span>关于平台</span>
          </router-link>
