</template>
<script>
import  "../sass/style.scss";
import axios from 'axios'
export default {
  name: 'Task',  
  data () {
    return {
    	info: null
    }
  },
  methods: {
    toInfo () {
      this.$router.push({ name: 'Infomation'})
    }
  },
  mounted () {
    axios
      /*.get('http://47.105.219.216/api/home/getGoodsNew')*/
      .get('http://yg.91chihu.com/api.php/member/getmemberdetail',{
		      params:{
		      	id:1
		      }
	      })
      .then(response => {
      	(this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      
      /*toGeneCode(){
      	this.$router.push({
      	path:"/gendCode",
      	query:{
      		id:this.id
      	}
      })
      }*/
      
      
  }
}
</script>


<style lang="scss" scoped>
 
</style>



