<template>
  <div>
    <Header title="加入会员"/>
    <div class="headImg">
      <div class="vipPrice">
        <div class="price flex_row">
          <span>VIP会员：</span>
          <i>268元</i>
        </div>
      </div>
      <div class="vipText">
        <p>权益说明：</p>
        <p>1. 自加入会员起所有活动商品均可享受8折优惠；</p>
        <p>2. 结结实实的好，可是打完球回家回家回家时款式简单的舞蹈教及时的回复速度和时间哈结束，及时的呼呼的时间；</p>
        <p>3. 接电话接电话哈，及时的回复饿好堵好的成绩；</p> 
        <p>4. 及时的回复饿是服务，胆红素和司法所开发和几号。</p>
      </div>
    </div>
    <div class="rechargeMain padd">
      <div class="chosePay charge">
        <div class="payTit">选择支付方式</div>
        <van-radio-group v-model="radio">
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/duhgs.png"/>
            <van-cell title="支付宝" clickable @click="radio = 1">
              <van-radio name="1" />
            </van-cell>
          </van-cell-group>
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/sxcdk.png"/>
            <van-cell title="微信" clickable @click="radio = 2">
              <van-radio name="2" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
    </div>
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row" @click="toRecharge">
        <span>去充值</span>
      </van-button>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return {
      data:[
        { value: '200元', id: 0 },
        { value: '100元', id: 1 },
        { value: '50元', id: 2 },
        { value: '20元', id: 3 },
        { value: '其他金额', id: 4 }
      ],
      money: null,
      flag: 0,
      radio: 0
    }
  },
  mounted () {
     // console.log(this.$route.params.id)
  },
  methods: {
    tableVan (it, index) {     
      this.flag = index
    },
    toRecharge () {
      /*this.$router.push({ 
        name: 'Toast',
        params:{ type: 2 }
      })*/
     axios({
      	url:"http://yg.91chihu.com/api.php/member/joinmember",
      	method:"get",
      	params:{
      			id:this.$route.params.id
      	}
      }).then(response => {
      	  (this.info = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 

  .headImg{
    width: 100%;
    height: 9.38rem;
    background: #fff;
    background: url('~@/assets/wsdds.jpg');
    background-size: 100% auto;
    padding: 0 0.3rem;
    .vipPrice{
      width: 100%; 
      padding-top: 3.13rem;     
      .price{
        width: 100%;
        height: 0.93rem;
        background: #fff;
        border-radius: 0.93rem;
        font-size: 0.3rem; 
        i{
          color: #e64535;
        }
      } 
    }
    .vipText{
      width: 100%;
      padding-top: 0.4rem;
      p{
        font-size: 0.28rem;
        color: #fff;
        line-height: 0.5rem;
        text-indent: -1em;
        margin-left: 1em;
      }
    }
  }
  .rechargeMain{
    width: 100%;
    background: #fff;
  }
  .btmBtn{
    width: 100%;
    height: 1.4rem;
    background: #fff;  
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: 0.88rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
  .charge.chosePay{
    padding: 0.1rem 0 0.5rem;
  }
</style>



