<template>
  <div>
    <Header :title="status==1?'余额':'佣金'"/>   
    <template>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        @change="tabHead"
      >
        <ul class="shopBox padd">
          <li class="shoplis flex_space" v-for="(item,index) in info" :key="index">
            <div class="bigImg flex_row">
              <p>{{ item.type>0? '-':'+' }}{{item.changenum}}<span style="color: #fff;">(元)</span></p>
            </div>
            <div class="rightShop flex_col">
              <div class="shopTit word">
                推广奖励  
              </div>
              <p class="p2">时间：{{item.addtime}}</p>
            </div>
          </li>
        </ul>
      </van-list>
    </template>
      
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return {
    	info:'',
    	nav:[
        {title: '余额', id: 1},
        {title: '佣金', id: 2}
      ],
      list: [],
      loading: false,
      finished: false,
      status: this.$route.query.status
    }
  },
  mounted () {
		//	console.log(this.status)
			if(this.status==1){
      	axios({
      	url:"http://192.168.1.192/duobao/api.php/balance/getbalance",
      	method:"get",
      	params:{
      			id:1
      	}
      }).then(response => {
      	(this.info = response.data)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error.response);
      }); 
      }else if(this.status==2){
      	axios({
      	url:"http://192.168.1.192/duobao/api.php/commission/getcommission",
      	method:"get",
      	params:{
      			id:1
      	}
      }).then(response => {
      	(this.info = response.data)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error.response);
      }); 
      }
  },
  //var s=this.status
  methods: {
    tabHead (index,title) {
     console.log(index,title)
      
          
      
    },
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  
  .shoplis{
    background: #fff;
    margin-top: 0.3rem;
    box-shadow: 0 0 0.1rem #ddd;
    .bigImg{
      width: 2.16rem;
      height: 1.26rem;
      border-radius: 0.1rem;
      background: #e64535;
      p{
        font-size: 0.44rem;
        color: #fff;
        span{
          font-size: 0.18rem;
          color:　#fff;
          padding-left: 0.1rem;
        }
      }
    }
    .rightShop{
      height: 100%;
      padding-right: 0.4rem;
      .shopTit{
        width: 100%;
        font-size: 0.32rem;
        line-height: 0.5rem;
        color: #e64535;
        text-align: right;
      }   
      .p2{
        font-size: 0.24rem;
        color: #666;
        text-align: right;
      }
    }
  }
</style>



