<template>
  <div>
    <Header title="中奖记录"/>
    
    <template>
      <van-list><!--v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"-->
        <ul class="shopBox padd" v-for="(item,index) in info" :key="index">
          <li class="shoplis flex"  >
            <div class="bigImg">
              <img :src="item.good_pic" alt="">
            </div>
            <div class="rightShop flex1" >
              <div class="shopTit word_wrap">
                {{item.good_name}}<!--Apple iPhone X (A1865) 64GB 深空灰色 移动联通电信4G手机  -->
              </div>
              <p class="p1">时间：{{item.wintime}}<!--2018-11-01-->  </p>
            </div>
          </li>
        </ul>
      </van-list>
    </template>      
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return { 
    	info:''
    }
  },
  mounted () {
  	var that=this
		axios.get('http://yg.91chihu.com/api.php/member/mywinlog',{
		      params:{
		      	id:this.$route.params.id
		      }
	      }).then(/*response => {
			      	(this.info = response.data.win)
			      	//console.log(this.info)
			      	console.log(response.data.win)
			      	}*/
			      	function(result){
					    //	console.log(result)
					    	that.info=result.data.win
					    	/*console.log(result)
					    	console.log(result.data)
								console.log(result.data.win)*/
    }
			     )
		      .catch(function (error) { // 请求失败处理
		        console.log(error);
		      });
  },
  methods: {
 
   /* onLoad() {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 500);
    }*/
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  
  .shoplis{
    background: #fff;
    margin-top: 0.3rem;
    padding: 0.4rem .3rem;
    box-shadow: 0 0 0.1rem #ddd;
    .bigImg{
      width: 1.65rem;
      height: 1.65rem;
      border-radius: .1rem;
      margin-right: 0.2rem;
      border: 1px solid #eee;
    }
    .rightShop{
      height: 100%;
      .shopTit{
        width: 100%;
        font-size: 0.28rem;
        line-height: 0.5rem;
        color: #e64535;
      }
      .p1{
        font-size: 0.26rem;
        color: #666;       
        line-height: 0.44rem;
        padding-top: 0.3rem;
      }    
    }
  }
</style>



