<template>
  <div >
    <div class="homeHead padd flex_row">
      <div class="homeLf flex1 flex">
        <div class="headImg">
          <img :src="infomsg.headimg" alt=""><!--src="~@/assets/dc.png"-->
        </div>
        <div class="honmInfo">
          <p class="p1">
            <b>{{infomsg.nickname}}</b><!--标题-->
            <span>{{infomsg.mark}}</span>
          </p>
          <p class="p2">ID: {{infomsg.recommendid}}</p><!--1235533-->
        </div>  
      </div>
      <div class="homeRt flex_col">
        <i class="icon"></i>
        <span>消息</span>
      </div>
    </div>

    <van-swipe :autoplay="2000" :show-indicators="false" >
      <van-swipe-item v-for="(item,index) in info" :key = "index">
        <img :src="item.pic"></img><!--{{image.pic}}-->   <!--src="~@/assets/timg.jpg"-->
      </van-swipe-item>
    </van-swipe>    
    <van-notice-bar
      text="足协杯战线连续第2年上演广州德比战，上赛季半决赛上恒大以两回合5-3的总比分淘汰富力。"
      
      @click="toNotice"
    />   <!--left-icon="volume-o"-->
    <div class="btmImage">
      <van-row gutter="24" >
        <van-col span="24">
          <router-link to="TaskList">
            <img src="~@/assets/cdrfgt.png" alt="">
          </router-link>
        </van-col>
        <van-col span="24">
          <router-link to="DreamTaskList">
            <img src="~@/assets/sdrftg.png" alt="">
          </router-link> 
        </van-col>
      </van-row>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import { Slider } from 'vant'
import axios from 'axios'
Vue.use(Slider)

export default {
  name: 'Home', 
  data () {
    return {      
    	info:'',
    	infomsg:''
    }
  },
  mounted () {
		var that=this
		axios({
      	url:"http://192.168.1.192/duobao/api.php/system/getlooppic",
      	method:"get",
      })
    .then(
		   function(result){
					    	that.info=result.data
								console.log(result.data)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      
      axios
      .get('http://192.168.1.192/duobao/api.php/member/getmemberdetail',{
		      params:{
		      	id:1
		      }
	      })
      .then(response => {
      	(this.infomsg = response.data.member)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });      
      
  },
  methods: {
    toNotice () {
      this.$router.push({ name: 'NoticeDet'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .van-swipe{
    width:100%;
    height: 3.62rem;
    background: #fff;
  }
  .btmImage{
    padding: 0.2rem 0.2rem 0;
    background: #fff;
  }
  .van-notice-bar{
    background: #fff;
  }
</style>



