<template>
  <div>
    <Header title="提现"/>
    <div class="headImg flex_col">
        <p>余额（元）</p>
        <p class="money">{{infos.balance}}</p>
    </div>

    <div class="rechargeMain padd">
      <div class="balanceBox flex_space">
        <div class="inputMoney flex_row">
          <van-field 
            v-model="money"
            label="提现金额："
            placeholder="请输入提现金额"
            @input="numPanduan"
          />
          <span>元</span>
        </div>
      </div>
      <div class="maskText">手续费：按照统一标准收取20%手续费</div>

      <div class="chosePay">
        <div class="payTit">选择提现途径</div>
        <van-radio-group v-model="radio" @change="handelChange">
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/duhgs.png"/>
            <van-cell title="支付宝" clickable @click="radio = 1">
              <van-radio name="1" />
            </van-cell>          
          </van-cell-group>
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/sxcdk.png"/>
            <van-cell title="微信" clickable @click="radio = 2">
              <van-radio name="2" />
            </van-cell>
          </van-cell-group>
          <van-cell-group class="flex_alicen pdLf">
            <img class="payIco" src="~@/assets/okjdh.png"/>
            <van-cell title="银行卡" clickable @click="radio = 3">
              <van-radio name="3" />
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
    </div>
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row" @click="toRecharge">
        <span>提现</span>
      </van-button>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

export default {
  name: 'TaskList',
  components: {
    Header
  },
  data () {
    return {
      data:[
        { value: '200元', id: 0 },
        { value: '100元', id: 1 },
        { value: '50元', id: 2 },
        { value: '20元', id: 3 },
        { value: '其他金额', id: 4 }
      ],
      id:this.$route.params.id,
      money: this.money,
      flag: 0,
      radio: 0,
      count: 0,
      infos:''
    }
  },
  mounted () {
   console.log(this.$route.params.id+"idididi")
    var that=this
   axios({
      	url:"http://yg.91chihu.com/api.php/member/withdraw",
      	method:"get",
      	params:{
      			id:this.$route.params.id
      	}
      }).then(response => {
      	(that.infos = response.data)
      	//console.log(response)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
  },
  methods: {
    handelChange(val) {
      this.count = val     
    },
    toRecharge () {
    	var th=this.count
   //   console.log(th+"zzz")
      var m=this.money*0.2
      var that=this
      if(this.count =='2'){
          axios({
      	url:"http://yg.91chihu.com/api.php/member/withdraw",
      	method:"get",
      	params:{
      			id:this.$route.params.id,
						money:m
      	}
      }).then(response => {
      	(that.info = response.data.member)
      	//console.log(response.balance)
      	}
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
      });
      }else{
      	/*支付宝*/
        this.$router.push({
          path: '/bankWithdraw', 
          query: {
            type: this.count,
            money:this.money,
            id:this.id
          }
        })
      }
    },
    numPanduan(){
    	console.log(this.infos)
    	console.log(event.target.value)
    	if(parseFloat(this.infos.balance)<parseFloat(event.target.value)){
    		alert("您的余额不足")
    	}
    }
    
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

  .headImg{
    width: 100%;
    height: 3rem;
    background: #f5c243;
    p{
      color: #fff;
      &:first-child{
        font-size: 0.3rem;
      }
       &:last-child{
        font-size: .7rem;
        padding-top: 0.15rem;
      }
    }
  }
  .rechargeMain{
    width: 100%;
    background: #fff;
    .maskText{
      color: #666;
      font-size: 0.28rem;
      padding-left: 0.3rem;
    }
    .balanceBox{
      width: 100%;
      height: 1.6rem;
      .balance{
        p:nth-of-type(1){
          font-size: 0.26rem;
          color: #999;
        }
        p:nth-of-type(2){
          color: #e64535;
          font-size: 0.28rem;
          font-weight: bold;
          padding-top: 0.1rem;
        }
      }
      .inputMoney{
        width: 100%;
      }
    }
    .moneyUl{
      width: 100%;
      li{
        width: 30%;
        height: 1.0rem;
        margin-bottom: 0.25rem;
        &:nth-of-type(2){
          margin: 0 5%;
        }
        &:nth-of-type(5){
          margin: 0 5%;
        }
        button{
          width: 100%;
          height: 100%;
          span{
            font-size: 0.3rem;
            color: #cfcece;
          }
        }
      }
      .checked{
        .moneyBtn{
          background: url('~@/assets/cbgedf.png') no-repeat center;
          background-size: 100% 100%;
          span{
            color: #e64535;
          }
        }
      }
    }
  }
  .btmBtn{
    width: 100%;
    height: 1.4rem;
    background: #fff;
    position: fixed;
    left: 0;
    bottom: 0rem;
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: .1rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
  
</style>



