<template>
  <div>
    <Header title="推广码"/>
    <div class="geneCode">
      <div class="geneBox">
        <div class="codeBox flex_col">
            <div class="bigImg">
              <img :src="info.code" alt="">
            </div>
            <!--<p>@臭貔貅爱美</p>-->
            <p>{{info.nickname}}</p>
        </div>
      </div>
      <div class="geneCodeText">
        <p class="p1">推广说明：</p>
        <p class="p2">圣诞节健身房和沃达斯科贷记卡是 接地气阿斯顿卡上粉底，时间的华师大数九寒冬速度户，救护车也是孤独啊。就是读书大近些年出的上诉</p>
      </div>
    </div>
    
    <div class="btmBtn flex_col padd">
      <van-button :disabled="false" type="danger" class="flex_row" @click="toRecode">
        <span>我要推广</span>
      </van-button>
    </div>

    <van-dialog
      v-model="show"
      show-cancel-button
      :lazy-render="false"
      :show-cancel-button="false"
      :show-confirm-button="false"
    >
      <div class="header flex_space">
        <i class="flex1 flex_row title">分享好友</i>
        <i class="icnofont" @click="show=false"> </i>
      </div>
      <div class="flex_row main">
        <div class="bigimg flex_col">
          <img src="@/assets/sxcdk.png" alt="">
          <span>微信好友</span>
        </div>
        <div class="bigimg flex_col">
          <img src="@/assets/edfrgh.png" alt="">
          <span>微信好友</span>
        </div>
      </div>
    </van-dialog>
  </div>
</template>
<script>
import Header from '@/components/Header'
import axios from 'axios'

//alert("123");
//alert(info.NickName)

export default {
  name: 'GeneCode',
  components: {
    Header
  },
  data () {
    return {     
    	info:'',
      show: false
    }
  },
  mounted () {
  	console.log(this.$route.params.id)
  	 var that=this
		axios({
      	url:"http://192.168.1.192/duobao/api.php/member/spreadcode",
      	method:"get",
      	params:{
      			id:this.$route.params.id
      		/*	name:this.$route.params.name*/
      	}
      }).then(
		   function(result){
					    //	console.log(result)
					    	that.info=result.data.map
								console.log(result.data.map)
		    }
     )
      .catch(function (error) { // 请求失败处理
        console.log(error);
        
      });  //	console.log(this.$route.params.name)
  },
  methods: {  
    toRecode () {
      this.show = true
    }  
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped> 
  .headers .van-hairline--bottom.van-nav-bar{
    background: none!important;
  }
  .geneCode{
    width: 100%;
    min-height: 93vh;
    background: url('~@/assets/edcfg.png');
    background-size: 100% auto;
    padding: 0.74rem 0.42rem 0;
    .geneBox{
      width: 100%;
      height: 4.38rem;
      background: #fff;
      padding: 0.2rem;      
      border-radius: 0.1rem;
      .codeBox{
        width: 100%;
        height: 100%;
        border: 1px solid #666;
        border-radius: 0.1rem;
        .bigImg{
          width: 2.0rem;
          height: 2.0rem;
        }
        p{
          font-size: 0.3rem;
          color: #333;
          padding-top: 0.3rem;
        }
      }
    }
    .geneCodeText{
      width: 100%;
      padding: 0.4rem 0.1rem 0;

      p{
        color: #fff;
        line-height: 0.44rem;
      }
    }
  }
  .btmBtn{
    width: 100%;
    height: 2.15rem;   
    position: fixed;
    left: 0;
    bottom: 0;
    button{
      width: 100%;
      height: 0.88rem;
      color: #fff;
      border-radius: 0.1rem;
      .van-icon.van-icon-phone{
        color: #fff;
        padding-right: 0.2rem;
        font-size: 0.3rem;
      }
      span{
        color: #fff;
        font-size: 0.3rem; 
      }
    }
  }
 
  .header{
    width: 100%;    
    padding: 0.3rem;
    .title{
      font-size: 0.32rem;
    }
    .icnofont{
      width: 0.3rem;
      height: 0.3rem;
      background: url('~@/assets/dcfrg.png');
      background-size: 100% auto;
    }
  }
  .main{
    width: 100%;
    padding: 0.36rem 0 0.9rem;
    .bigimg{  
      &:nth-of-type(1){
        padding-right: 1.2rem;
      }
      img{
        width: 0.89rem;
        height: 0.89rem;
      }
      span{
        font-size: 0.30rem;
        color: #333;
        padding-top: 0.15rem;
      }
    }
  }
</style>



